import Box from './components/Box/Box';
import { ThemeContextWrapper } from './context/ThemeContext';
import './App.scss';

function App() {
    return (
        <ThemeContextWrapper>
            <div className="App">
                <div className="wrapper" data-testid="wrapper">
                    <div className="box-container box-container--1">
                        <Box className="box-1">1</Box>
                        <Box className="box-2">2</Box>
                        <Box className="box-3">3</Box>
                        <Box className="box-4">4</Box>
                    </div>
                    <div className="box-container box-container--2">
                        <Box className="box-5">5</Box>
                        <Box className="box-6">6</Box>
                        <Box className="box-7">7</Box>
                        <Box className="box-8">8</Box>
                        <Box className="box-9">9</Box>
                    </div>
                </div>
            </div>
        </ThemeContextWrapper>
    );
}

export default App;
