import React, { useContext, useEffect, useState } from 'react';
import classNames from 'classnames';
import { ThemeContext } from '../../context/ThemeContext';
import './Box.scss';

const Box = ({ children, className }) => {
    const [style, setStyle] = useState({});
    const themeContext = useContext(ThemeContext);

    useEffect(() => {
        if (!themeContext.theme) return;
        // Select a random color from the current theme
        const color = themeContext.theme[Math.floor(Math.random() * themeContext.theme.length)];
        setStyle({ backgroundColor: `#${color}` });
    }, [themeContext]);

    const handleOnClick = () => {
        themeContext.changeTheme();
    };

    return (
        <div className={classNames('box', className)} onClick={handleOnClick} style={style}>
            {children}
        </div>
    );
};

export default Box;
